.Modal {
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(200, 200, 200, 0.4),
            rgba(200, 200, 200, 0.4));
    display: flex;
    justify-content: flex-end;
    z-index: 1000;
    position: fixed;
    top: 0;
    left: 0;
}

.Btn {
    margin-right: -400px;
    width: 300px;
    height: 50px;
    font-size: large;
    color: #fff;
    font-weight: 700;
    background-color: #20A39E;
    border: none;
}

.BtnClick {
    margin-right: 60px;
    width: 300px;
    height: 50px;
    font-size: large;
    color: #fff;
    font-weight: 700;
    background-color: #87CEEB;
    border: none;
}